<template>
  <div id="new-group-chat-page">
    <h3>
      <van-icon
        name="arrow-left"
        color="rgb(107, 107, 107)"
        @click="routerGoBack"
      />
      <span class="set">
        <span>{{ groupName }}</span>
        <img
          src="../../assets/image/more.png"
          @click="$router.push('/groupmessage')"
        />
      </span>
    </h3>
    <p class="announcement" v-if="isKnowAnnouncement">
      <span>{{ announcement }}</span>
      <span @click="knowAnnouncement">未读</span>
    </p>
    <main @click="handle" ref="main">
      <div class="content">
        <p>{{ initCreateTime }}</p>
        <p v-show="groudOwner == userId">
          你邀请了{{ promptWords }}加入了{{ groupName }}
        </p>
        <p v-show="groudOwner !== userId">你加入了{{ groupName }}</p>
      </div>
      <div class="messages" v-show="textAllMessagesArr !== [] ? true : false">
        <div v-for="(item, index) in textAllMessagesArr" :key="index">
          <div v-show="item.sendPeople == userId" class="right">
            <img
              v-show="item.sendPeopleImg !== undefined"
              :src="item.sendPeopleImg"
            />
            <img
              v-show="item.sendPeopleImg == undefined"
              src="../../assets/image/images/default_hd_avatar.png"
            />
            <span>{{ item.text }}</span>
          </div>
          <div v-show="item.sendPeople !== userId" class="left">
            <img
              v-show="item.sendPeopleImg !== undefined"
              :src="item.sendPeopleImg"
            />
            <img
              v-show="item.sendPeopleImg == undefined"
              src="../../assets/image/images/default_hd_avatar.png"
            />
            <span>{{ item.text }}</span>
          </div>
        </div>
      </div>
    </main>
    <footer>
      <div class="choose">
        <div class="left">
          <img src="../../assets/image/ChatDesView/voice.png" />
        </div>
        <div class="container">
          <input
            v-model="inputValue"
            type="text"
            @keydown="isSendOneTextMessage"
          />
        </div>
        <div class="right">
          <span>
            <img src="../../assets/image/ChatDesView/emote.png" />
          </span>
          <span @click="handleClick" v-if="!inputValue.trim()">
            <img src="../../assets/image/ChatDesView/more.png" />
          </span>
          <button v-else @click="sendOneTextMessage">发送</button>
        </div>
      </div>
      <transition name="slide">
        <div class="list" v-show="showFunctionList">
          <div class="item">
            <img src="../../assets/image/ChatDesView/photography.png" />
            <span>相册</span>
          </div>
          <div class="item" @click="takePhoto">
            <img src="../../assets/image/ChatDesView/takephoto.png" />
            <span>拍摄</span>
          </div>
          <div class="item">
            <img src="../../assets/image/ChatDesView/phone.png" />
            <span>视频通话</span>
          </div>
          <div class="item" @click="map">
            <img src="../../assets/image/ChatDesView/place.png" />
            <span>位置</span>
          </div>
          <div class="item">
            <img src="../../assets/image/ChatDesView/redpack.png" />
            <span>红包</span>
          </div>
          <div class="item">
            <img src="../../assets/image/ChatDesView/money.png" />
            <span>转账</span>
          </div>
          <div class="item">
            <img src="../../assets/image/ChatDesView/talk.png" />
            <span>语音输入</span>
          </div>
          <div class="item">
            <img src="../../assets/image/ChatDesView/collect.png" />
            <span>我的收藏</span>
          </div>
        </div>
      </transition>
    </footer>
  </div>
</template>

<script>
import WebIM from "../../utils/WebIM";
import eventBus from ".././eventbus";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      groupName: "",
      //   群组刚刚创建的时间
      initCreateTime: "",
      // 今天的时间
      todayTime: new Date(),
      //   新建群聊的提示语
      promptWords: "",
      //  键盘输入框
      inputValue: "",
      // 信息来往列表
      textAllMessagesArr: [],
      // 显示可操作功能列表
      showFunctionList: false,
      groupId: "",
      // 谁创建的群聊
      groudOwner: "",
      // 发布新的公告
      announcement: "",
      // 是否显示群公告
      isKnowAnnouncement: false,
      activedKey: {
        contact: "",
        group: "",
        chatroom: "",
      },
    };
  },
  computed: {
    ...mapState({
      newCreateGroupId: (state) => state.home.newCreateGroupId,
      cacheClickGroupId: (state) => state.home.cacheClickGroupId,
      userId: (state) => state.user.userId,
      textMessagesArr: (state) => state.groupchat.textMessagesArr,
      otherPeopleTextMessagesArr: (state) =>
        state.groupchat.otherPeopleTextMessagesArr,
      isUpdateGourpAnnouncement: (state) =>
        state.home.isUpdateGourpAnnouncement,
    }),
  },
  watch: {
    userId() {
      this.getGroupId();
    },
    isUpdateGourpAnnouncement(newV) {
      if (newV) {
        this.isKnowAnnouncement = JSON.parse(
          sessionStorage.getItem("isUpdateGourpAnnouncement")
        );
        if (this.isKnowAnnouncement == true) {
          this.getAnnouncement();
        }
      }
    },
    textMessagesArr() {
      this.getTextAllMessagesArr();
    },
    otherPeopleTextMessagesArr() {
      this.getTextAllMessagesArr();
    },
  },
  created() {
    this.getGroupId();

    eventBus.$on("location", function (data) {}.bind(this));
  },
  methods: {
    ...mapMutations([
      "setTextMessagesArr",
      "setIsKnowGroupMessages",
      "setGroudId",
    ]),
    // 获取群组ID
    getGroupId() {
      if (this.userId !== "") {
        if (this.newCreateGroupId !== "") {
          this.groupId = this.newCreateGroupId;
        } else if (
          this.newCreateGroupId == "" &&
          this.cacheClickGroupId !== ""
        ) {
          this.groupId = this.cacheClickGroupId;
        } else if (
          this.newCreateGroupId == "" &&
          this.cacheClickGroupId == ""
        ) {
          this.groupId = sessionStorage.getItem("groupInfoCacheSave");
        }
        this.getGroupInfo();
        this.setIsKnowGroupMessages(this.groupId);
        this.getTextAllMessagesArr();
        this.isKnowAnnouncement = JSON.parse(
          sessionStorage.getItem("isUpdateGourpAnnouncement")
        );
        if (this.isKnowAnnouncement == true) {
          this.getAnnouncement();
        }
      }
    },
    // 获取群聊公告相关信息
    getAnnouncement() {
      let option = {
        groupId: this.groupId,
      };
      WebIM.conn.fetchGroupAnnouncement(option).then((res) => {
        this.announcement = res.data.announcement;
      });
    },
    // 获取群聊的相关信息
    getGroupInfo() {
      // let id =
      //   this.newCreateGroupId !== ""
      //     ? this.newCreateGroupId
      //     : sessionStorage.getItem("newCreateGroupId");
      let option = {
        // 群组 ID
        groupId: this.groupId,
      };
      //   获取群信息和创建时间加入的时间戳
      WebIM.conn.getGroupInfo(option).then((res) => {
        this.groudOwner = res.data[0].owner;
        this.groupName = res.data[0].name;
        let date = new Date(res.data[0].affiliations[0].joined_time);
        let year = date.getFullYear();
        let mounth = date.getMonth();
        let day = date.getDate();
        let nowYear = this.todayTime.getFullYear();
        let nowMounth = this.todayTime.getMonth();
        let nowDay = this.todayTime.getDate();
        let hours =
          date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
        let minutes =
          date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
        if (year == nowYear && mounth == nowMounth && day == nowDay) {
          this.initCreateTime = `${hours}:${minutes}`;
        } else {
          this.initCreateTime = `${mounth}月${day}日 ${hours}:${minutes}`;
        }
      });
      //   获取群成员信息
      let option1 = {
        pageNum: 1,
        pageSize: 200,
        groupId: this.groupId,
      };
      WebIM.conn.listGroupMembers(option1).then((res) => {
        let newArr = res.data.filter((item) => {
          if (item.member) {
            return true;
          } else {
            return false;
          }
        });
        newArr.map((item, i) => {
          WebIM.conn.fetchUserInfoById(item.member).then((r) => {
            if (i !== newArr.length - 1) {
              this.promptWords += r.data[item.member].nickname + "、";
            } else {
              this.promptWords += r.data[item.member].nickname;
            }
          });
        });
      });
    },
    // 发送一条群聊会话
    sendOneTextMessage() {
      // 发送文本消息。
      let option = {
        // 设置消息类型。
        type: "txt",
        // 设置消息内容。
        msg: this.inputValue,
        // 设置消息接收方，单聊为对方用户 ID，群聊和聊天室分别为群组 ID 和聊天室 ID。
        to: this.groupId,
        // 设置会话类型，单聊、群聊和聊天室分别为 `singleChat`、`groupChat` 和 `chatRoom`，默认为单聊。
        chatType: "groupChat",
      };
      // 创建文本消息。
      let msg = WebIM.message.create(option);
      // 调用 `send` 方法发送该文本消息。
      WebIM.conn
        .send(msg)
        .then(() => {
          let obj = {
            text: this.inputValue,
            time: new Date().getTime(),
            sendPeople: this.userId,
            to: this.groupId,
          };
          this.setTextMessagesArr(obj);
          this.inputValue = "";
          this.setGroudId(this.groupId);
          console.log("Send message success");
        })
        .catch((e) => {
          console.log("Send message fail");
        });
    },
    // 监听是否按下enter发送信息
    isSendOneTextMessage(e) {
      if (e.keyCode == 13) {
        this.sendOneTextMessage();
      }
    },
    // 获取群聊会话的相关信息
    getTextAllMessagesArr() {
      let newTextMessagesArr = this.textMessagesArr.filter((item) => {
        if (item.to == this.groupId) {
          console.log(this.groupId, item.to);
          return true;
        } else {
          return false;
        }
      });
      let newOtherPeopleTextMessagesArr =
        this.otherPeopleTextMessagesArr.filter((item) => {
          if (item.to == this.groupId) {
            return true;
          } else {
            return false;
          }
        });
      let arr = [...newTextMessagesArr, ...newOtherPeopleTextMessagesArr];
      // 按时间顺序排序
      arr = this.geTimeSequenceArr(arr);
      // console.log(arr);
      arr.map((item) => {
        WebIM.conn.fetchUserInfoById(item.sendPeople).then((res) => {
          item.sendPeopleImg = res.data[item.sendPeople].avatarurl;
        });
      });
      setTimeout(() => {
        this.textAllMessagesArr = arr;
        this.$refs["main"].scrollTop = this.$refs["main"].scrollHeight;
      }, 800);
    },
    // 将会话信息按时间顺序排列
    geTimeSequenceArr(arr) {
      if (arr !== []) {
        let temp = "";
        for (let i = 0; i < arr.length - 1; i++) {
          for (let j = 0; j < arr.length - i; j++) {
            if (arr[j + 1] !== undefined) {
              if (arr[j].time > arr[j + 1].time) {
                temp = arr[j];
                arr[j] = arr[j + 1];
                arr[j + 1] = temp;
              }
            }
          }
        }
        return arr;
      }
    },
    // 是否已读群聊公告
    knowAnnouncement() {
      let obj = {
        updateGroupId: this.groupId,
        boo: false,
      };
      this.isKnowAnnouncement = false;
      this.$store.commit("setIsUpdateGourpAnnouncement", obj);
    },
    // 跳转回原页面
    routerGoBack() {
      this.setIsKnowGroupMessages(this.groupId);
      this.$router.push({
        path: "/",
      });
    },
    // 跳转到摄像拍摄页面
    takePhoto() {
      this.$router.push("/take");
    },
    // 跳转到地图页面
    map() {
      this.$router.push("/map");
    },
    // 群公告是否显示
    handle() {
      this.showFunctionList = false;
    },
    // 群公告按钮点击表示已读
    handleClick() {
      this.showFunctionList = true;
    },
  },
};
</script>

<style lang="scss" scoped>
#new-group-chat-page {
  width: 100vw;
  height: 100vh;
  color: rgb(164, 164, 164);
  background-color: var(--theme-background-black);
  h3 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 45px;
    padding: 10px 10px 0 10px;
    background-color: rgb(17, 17, 17);
    border-bottom: 1px solid rgb(52, 52, 52);
    .set {
      width: 60%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      text-align: center;
      color: #eee;
      img {
        width: 20px;
      }
    }
  }

  .announcement {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 45px;
    line-height: 45px;
    padding: 5px 15px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    background-color: rgb(67 67 67);
    span {
      &:last-child {
        height: 30px;
        line-height: 30px;
        border-radius: 8px;
        padding: 0px 8px;
        background-color: #515151;
      }
    }
  }
  main {
    width: 100%;
    height: 88vh;
    overflow: scroll;
    padding-bottom: 80px;
    .content {
      p {
        padding-top: 10px;
        text-align: center;
        font-size: 12px;
      }
    }
    .messages {
      .right {
        overflow: hidden;
        margin-top: 10px;
        span {
          position: relative;
          float: right;
          padding: 0 10px;
          height: 40px;
          line-height: 40px;
          background-color: #05c160;
          border-radius: 5px;
          color: #fff;
          &::after {
            position: absolute;
            right: -14px;
            top: 13px;
            content: "";
            height: 0;
            width: 0;
            border-width: 7px 7px;
            border-style: solid;
            border-color: transparent transparent transparent #05c160;
            display: inline-block;
          }
        }
        img {
          float: right;
          width: 40px;
          margin: 0 15px;
          border-radius: 8px;
        }
      }
      .left {
        overflow: hidden;
        margin-top: 10px;
        span {
          position: relative;
          float: left;
          padding: 0 10px;
          height: 40px;
          line-height: 40px;
          background-color: #05c160;
          border-radius: 5px;
          color: #fff;
          &::after {
            position: absolute;
            left: -14px;
            top: 13px;
            content: "";
            height: 0;
            width: 0;
            border-width: 7px 7px;
            border-style: solid;
            border-color: transparent #05c160 transparent transparent;
            display: inline-block;
          }
        }
        img {
          float: left;
          width: 40px;
          margin: 0 15px;
          border-radius: 8px;
        }
      }
    }
  }

  footer {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: center;
    .choose {
      display: flex;
      width: 100%;
      height: 45px;
      background-color: #1e1e1e;
      padding: 0 10px;

      div {
        margin: auto;
      }

      .left {
        flex: 0 0 10%;

        img {
          width: 100%;
        }
      }

      .container {
        flex: 0 0 70%;
        // border: 1px brown solid;

        input {
          width: 85%;
          height: 29px;
          overflow: hidden;
          background-color: rgba(128, 128, 128, 0.445);
          border: none;
        }
      }

      .right {
        flex: 0 0 20%;
        display: flex;
        span {
          flex: 1;

          img {
            width: 100%;
          }
        }
        button {
          margin: 0 2px;
          background-color: #05c160;
          color: #fff;
          font-size: 12px;
          border: none;
          border-radius: 5px;
        }
      }
    }

    .list {
      display: flex;
      flex-wrap: wrap;
      position: relative;
      width: 100%;
      color: #fff;
      background-color: #1e1e1e;

      .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 25%;
        height: 80px;
        // margin: auto;
        text-align: center;
        font-size: 12px;
        box-sizing: border-box;
        padding: 10px 0;

        img {
          width: 35px;
        }
        span {
          margin-top: 10px;
        }
      }
    }
  }
}
</style>